footer {
    font-family: "Inter";
    font-size: 13px;
    text-align: center;
    color: #B8B8B8;
}

#upButton {
  display: none; 
  position: fixed;
  bottom: 20px; 
  right: 30px;
  z-index: 99;
  font-size: 28px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 4px solid black;
  color: black;
  background-color: #6E6E6E; 
  cursor: pointer;
}
  
#upButton:hover {
  background-color: #555;
}